<template>
  <div class="rate-demo">
    <div class="rate-demo__item">
      <h2 id="rate-ping-fen">
        <a href="#rate-ping-fen" aria-hidden="true" class="header-anchor"></a>
        Rate 
      </h2>
      <p> <a href="http://element.eleme.io/#/zh-CN/component/rate#rate-ping-fen" target="_blank">Element Rate </a></p>
    </div>
    <div class="rate-demo__item">
      <h3 id="ji-chu-yong-fa">
        <a href="#ji-chu-yong-fa" aria-hidden="true" class="header-anchor"></a>
        
      </h3>
      <demo :multiple="true">
        <template slot="source">
          <div class="block">
            <span class="demonstration"></span>
            <vm-rate v-model="valueA1"></vm-rate>
          </div>
          <div class="block">
            <span class="demonstration"></span>
            <vm-rate
                v-model="valueA2"
                :colors="colorsA2">
            </vm-rate>
          </div>
        </template>
        <template slot="description">
           <code>colors</code>  <code>low-threshold</code>
           <code>high-threshold</code> <i class="new"><code>low-threshold</code>  <code>high-threshold</code> 
          <code>colors</code>  <code>iconClasses</code></i>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeA"><code class="html javascript"></code></pre>
        </template>
      </demo>
    </div>
    <div class="rate-demo__item">
      <h3 id="#fu-zhu-wen-zi">
        <a href="#fu-zhu-wen-zi" aria-hidden="true" class="header-anchor"></a>
        
      </h3>
      <p></p>
      <demo :multiple="true">
        <template slot="source">
          <div class="block">
            <span class="demonstration"></span>
            <vm-rate
                v-model="valueB1"
                show-text
                :texts="['', '', '', '', '']"></vm-rate>
          </div>
          <div class="block">
            <span class="demonstration"> <i class="new">()</i></span>
            <vm-rate
                v-model="valueB2"
                :show-text="true"
                text-type="number"
                text-template="{value}"></vm-rate>
          </div>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeB"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
           <code>show-text</code>  <code>texts</code> <code>texts</code>
           <code>max</code><i class="new"> <code>text-type="number"</code>  <code>text-template="{value}"</code>
          </i>
        </template>
      </demo>
    </div>
    <div class="rate-demo__item">
      <h3 id="qi-ta-icon">
        <a href="#qi-ta-icon" aria-hidden="true" class="header-anchor"></a>
         icon
      </h3>
      <p> icon </p>
      <demo>
        <template slot="source">
          <vm-rate
              v-model="valueC1"
              :iconClasses="['vm-icon-sad', 'vm-icon-neutral-full', 'vm-icon-smile']"
              voidIconClass="vm-icon-neutral-empty"
              disabledVoidIconClass="vm-icon-neutral-full"
              :colors="['#99A9BF', '#F7BA2A', '#FF9900']"></vm-rate>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeC"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
           <code>icon-classes</code>  3  <code>void-icon-class</code> <i
            class="new"> <code>disabledVoidIconClass</code>
          </i>
        </template>
      </demo>
    </div>
    <div class="rate-demo__item">
      <h3 id="zhi-du">
        <a href="#zhi-du" aria-hidden="true" class="header-anchor"></a>
        
      </h3>
      <p></p>
      <demo :multiple="true">
        <template slot="source">
          <div class="block">
            <span class="demonstration"> <i class="new">()</i></span>
            <vm-rate
                v-model="valueD1"
                disabled
                :allow-half="true"
                show-text
                text-color="#ff9900"
                text-template="{value}"></vm-rate>
          </div>
          <div class="block">
            <span class="demonstration"> <i class="new">()</i></span>
            <vm-rate
                v-model="valueD1"
                disabled
                :allow-half="false"
                show-text
                text-color="#ff9900"
                text-template="{value}"></vm-rate>
          </div>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeD"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
           <code>disabled</code>  <code>show-text</code> <code>text-template</code>
           <code>{value}</code>
          <code>{value}</code> <i class="new"> <code>allow-half="true"</code> 
          <code>text-template="{value}"</code></i>
        </template>
      </demo>
    </div>
    <div class="rate-demo__item">
      <h3 id="ban-xing">
        <a href="#ban-xing" aria-hidden="true" class="header-anchor"></a>
         <i class="new">()</i>
      </h3>
      <p> .5 </p>
      <demo :multiple="true">
        <template slot="source">
          <div class="block">
            <span class="demonstration"></span>
            <vm-rate
                v-model="valueE1"
                :allow-half="true"></vm-rate>
          </div>
          <div class="block">
            <span class="demonstration"></span>
            <vm-rate
                v-model="valueE2"
                :allow-half="true"
                show-text></vm-rate>
          </div>
          <div class="block">
            <span class="demonstration"></span>
            <vm-rate
                v-model="valueE3"
                :allow-half="true"
                show-text
                text-type="number"
                text-color="#ff9900"
                text-template="{value}"></vm-rate>
          </div>
          <div class="block">
            <span class="demonstration">10</span>
            <vm-rate
                v-model="valueE4"
                :max="10"
                :allow-half="true"
                show-text></vm-rate>
          </div>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeD"><code class="html javascript"></code></pre>
        </template>
        <template slot="description">
           <code>allow-half</code>  <code>show-text</code> <code>text-type="number"</code>
            <code>text-template</code>  <code>{value}</code>
          <code>{value}</code>  <code>:max="10"</code>
        </template>
      </demo>
    </div>
    <div class="rate-demo__item">
      <h3 id="attributes">
        <a href="./#attributes" aria-hidden="true" class="header-anchor"></a>
        Attributes
      </h3>
      <table class="table">
        <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        </thead>
        <tbody>
        <tr>
          <td>max</td>
          <td></td>
          <td>number</td>
          <td></td>
          <td>5</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td></td>
          <td>boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>allow-half</td>
          <td></td>
          <td>boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>low-threshold</td>
          <td></td>
          <td>number</td>
          <td></td>
          <td>2</td>
        </tr>
        <tr>
          <td>high-threshold</td>
          <td></td>
          <td>number</td>
          <td></td>
          <td>4</td>
        </tr>
        <tr>
          <td>colors</td>
          <td>icon  3  3 </td>
          <td>array</td>
          <td></td>
          <td>['#F7BA2A', '#F7BA2A', '#F7BA2A']</td>
        </tr>
        <tr>
          <td>void-color</td>
          <td> icon </td>
          <td>string</td>
          <td></td>
          <td>#C6D1DE</td>
        </tr>
        <tr>
          <td>disabled-void-color</td>
          <td> icon </td>
          <td>string</td>
          <td></td>
          <td>#EFF2F7</td>
        </tr>
        <tr>
          <td>icon-classes</td>
          <td>icon  3  3 </td>
          <td>array</td>
          <td></td>
          <td>['el-icon-star-on', 'el-icon-star-on','el-icon-star-on']</td>
        </tr>
        <tr>
          <td>void-icon-class</td>
          <td> icon </td>
          <td>string</td>
          <td></td>
          <td>el-icon-star-off</td>
        </tr>
        <tr>
          <td>disabled-void-icon-class</td>
          <td> icon </td>
          <td>string</td>
          <td></td>
          <td>el-icon-star-on</td>
        </tr>
        <tr>
          <td>show-text</td>
          <td></td>
          <td>boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>text-color</td>
          <td></td>
          <td>string</td>
          <td></td>
          <td>#1F2D3D</td>
        </tr>
        <tr>
          <td>texts</td>
          <td></td>
          <td>array</td>
          <td></td>
          <td>['', '', '', '', '']</td>
        </tr>
        <tr>
          <td>text-template</td>
          <td></td>
          <td>string</td>
          <td></td>
          <td>{value}</td>
        </tr>
        <tr>
          <td>text-type <i class="new"></i></td>
          <td></td>
          <td>string</td>
          <td>number</td>
          <td>text</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="rate-demo__item">
      <h3 id="event">
        <a href="./#event" aria-hidden="true" class="header-anchor"></a>
        Events
      </h3>
      <table class="table">
        <thead>
        <th></th>
        <th></th>
        <th></th>
        </thead>
        <tbody>
        <tr>
          <td>change</td>
          <td></td>
          <td></td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
  import { sourcecodeA, sourcecodeB, sourcecodeC, sourcecodeD } from './template.js'
  import Demo from './Demo.vue'

  export default {
    data () {
      return {
        valueA1: 0,
        valueA2: 0,
        colorsA2: ['#99A9BF', '#F7BA2A', '#FF9900'],
        valueB1: 3,
        valueB2: 3,
        valueC1: 0,
        valueD1: 3.7,
        valueD2: 3.7,
        valueE1: 0,
        valueE2: 0,
        valueE3: 0,
        valueE4: 0,
        sourcecodeA,
        sourcecodeB,
        sourcecodeC,
        sourcecodeD
      }
    },
    methods: {
      handleChange (val) {
        console.log(val);
      }
    },
    components: {
      Demo
    }
  }
</script>
<style lang="less" rel="stylesheet/less">
  .rate-demo {
    h2, h3 {
      font-weight: 400;
      color: #1f2f3d;
    }
    h2 {
      font-size: 28px;
      margin: 0
    }
    h3 {
      font-size: 22px;
      margin: 45px 0 15px;
    }
    h3, h2 {
      &:hover {
        a {
          opacity: .4;
        }
      }
      a {
        float: left;
        margin-left: -20px;
        opacity: 0;
        cursor: pointer;
        color: #4078c0;
        text-decoration: none;
        &:hover {
          opacity: .4;
        }
      }
    }
    p {
      font-size: 14px;
      color: #5e6d82;
      line-height: 1.5em;
    }
    .new {
      font-style: normal;
      color: #FF4949;
      code {
        background-color: #FF4949 !important;
        color: #fff !important;
      }
    }
    .table {
      border-collapse: collapse;
      width: 100%;
      background-color: #fff;
      color: #5e6d82;
      font-size: 14px;
      margin-bottom: 45px;
      line-height: 1.5em;
      th {
        text-align: left;
        border-top: 1px solid #eaeefb;
        background-color: #eff2f7;
        white-space: nowrap;
      }
      th, td {
        border-bottom: 1px solid #eaeefb;
        padding: 10px;
        max-width: 250px;
      }
    }
  }
</style>



